@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Unbounded:wght@200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: url(/PogadaApp/static/media/thunderstorm.1c75d049.jpg), url(/PogadaApp/static/media/rain.751a1f82.jpg);
  background-position: center 0px;
  background-size: 2000px;
  background-repeat: no-repeat;
  transition: 2.5s;
}

body.drizzle {
  background-image: url(/PogadaApp/static/media/drizzle.0ca0231c.jpg);
  transition: 2.5s;
}

body.rain {
  background-image: url(/PogadaApp/static/media/rain.751a1f82.jpg);
  transition: 2.5s;
}

body.snow {
  background-image: url(/PogadaApp/static/media/snow.17ae114d.jpg);
  transition: 2.5s;
}

body.rain {
  background-image: url(/PogadaApp/static/media/rain.751a1f82.jpg);
  transition: 2.5s;
}

body.thunderstorm {
  background-image: url(/PogadaApp/static/media/thunderstorm.1c75d049.jpg);
  transition: 2.5s;
}

body.mist,
body.smoke,
body.haze,
body.fog {
  background-image: url(/PogadaApp/static/media/fog.ef7c8c8b.jpg);
  transition: 2.5s;
}

body.dust,
body.sand,
body.ash {
  background-image: url(/PogadaApp/static/media/dust.e08120d2.jpg);
  transition: 2.5s;
}

body.squall {
  background-image: url(/PogadaApp/static/media/squall.70aac00f.jpg);
  transition: 2.5s;
}

body.tornado {
  background-image: url(/PogadaApp/static/media/tornado.7f0e756f.jpg);
  transition: 2.5s;
}

body.clear {
  background-image: url(/PogadaApp/static/media/clear.ad8afa67.jpg);
  transition: 2.5s;
}

body.clouds {
  background-image: url(/PogadaApp/static/media/clouds.eb8311eb.jpg);
  transition: 2.5s;
}

a {
  text-decoration: none;
  color: rgb(215, 200, 255);
}

a:hover {
  text-decoration: underline;
}

.App {
  background-color: #8b8a8a89;
  width: 400px;
  min-height: 600px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: rgb(250, 235, 235);
  text-shadow: 2px 2px black;
  transition: 0.5s
}

.App.inactive {
  min-height: 100px;
  height: 100px;
  transition: 0.5s
}

.App__search-component {
  margin-top: 30px
}

.App__input input {
  margin-right: 20px;
  width: 200px;
  height: 40px;
  font-size: 20px;
  border-radius: 7px;
  padding: 5px
}

.App__button {
  width: 100px;
  height: 40px;
  font-size: 20px;
  border-style: none;
  border-radius: 7px;
  cursor: pointer;
  background-color: rgb(231, 231, 231);
  transition: 0.3s;
}

.App__button:hover {
  background-color: rgb(141, 141, 141);
}

.App__button:active {
  color: rgb(87, 87, 87);
}

.App__view {
  margin-top: 10px;
  width: 320px;
}

.App__view-time {
  font-size: 15px;
}

.App__view-title {
  margin: 40px 0 0 0;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  justify-content: space-between;
  align-items: center;
}

.App__view-title h1 {
  letter-spacing: 2px;
}

.App__view-data {
  display: flex;
  flex-direction: column;
}

.App__view-data-general {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 30px;
}

.App__view-temp {
  font-size: 40px;
  font-weight: bold;
}

.App__view-sun {
  margin-top: 10px;
  display: flex;
  justify-content: space-around;
  font-size: 17px;
}

.App__view-sun img {
  width: 70px;
}

.App__view-details {
  margin: 10px 30px 0;
  background-color: rgba(101, 102, 107, 0.363);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  border-radius: 10px;
  padding: 0 15px 15px 15px;
  margin-bottom: 30px;
}


.App__view-pressure {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.App__view-pressure img {
  width: 30px;
  margin-right: 10px;
}

.App__view-windspeed {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.App__view-windspeed img {
  width: 30px;
  margin-right: 10px;
}

.App__view-clouds {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.App__view-clouds img {
  width: 30px;
  margin-right: 10px;
}

.App__view-humidity {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.App__view-humidity img {
  width: 30px;
  margin-right: 10px;
}
